<template>
  <nav class="navbar fixed-top navbar-expand-lg navbar-dark bg-primary">
    <div class="container">
      <router-link class="navbar-brand" :to="{ name: 'Home' }"
        >CiudadBusca.me</router-link
      >
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <form class="form-inline my-2 my-lg-0 ml-auto" @submit.prevent="search">
          <div class="input-group">
            <input
              type="search"
              class="form-control"
              placeholder="Buscar"
              aria-label="Buscar"
              aria-describedby="search"
            />
            <div class="input-group-append">
              <button class="btn btn-success" type="submit" id="search">
                Buscar
              </button>
            </div>
          </div>
        </form>
        <ul class="navbar-nav ml-auto">
          <li class="nav-item" v-if="!loggIn">
            <router-link class="nav-link" :to="{ name: 'Logup' }"
              >Registro</router-link
            >
          </li>
          <li class="nav-item" v-if="!loggIn">
            <router-link class="nav-link" :to="{ name: 'Login' }"
              >Entrar</router-link
            >
          </li>
          <li class="nav-item" v-if="loggIn">
            <router-link class="nav-link" :to="{ name: 'Wall' }"
              >Muro</router-link
            >
          </li>
          <li class="nav-item" v-if="loggIn">
            <router-link class="nav-link" :to="{ name: 'SMS' }"
              >SMS</router-link
            >
          </li>
          <li class="nav-item" v-if="loggIn">
            <router-link class="nav-link" :to="{ name: 'Mentions' }"
              >Menciones</router-link
            >
          </li>
          <li class="nav-item dropdown" v-if="loggIn">
            <a
              class="nav-link"
              href="#"
              id="navbarDropdown"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
              >{{ user.displayName ? user.displayName : user.email }}
              <img
                :src="
                  user.photoURL
                    ? user.photoURL
                    : `https://res.cloudinary.com/dbszizqh4/image/upload/v1592198427/images_lvwix2.png`
                "
                :alt="user.displayName ? user.displayName : user.email"
                class="rounded-circle img-avatar ml-1"
              />
            </a>
            <div
              class="dropdown-menu dropdown-menu-right"
              aria-labelledby="navbarDropdown"
            >
              <router-link class="dropdown-item" :to="{ name: 'Profile' }"
                >Perfil</router-link
              >
              <router-link class="dropdown-item" :to="{ name: 'MyPages' }"
                >Mis sitios</router-link
              >
              <router-link class="dropdown-item" :to="{ name: 'CreatePage' }"
                >Crear sitio</router-link
              >
              <div class="dropdown-divider"></div>
              <button class="dropdown-item" @click="logout">Salir</button>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import { fb } from "@/firebase";

export default {
  name: "TheNavbar",
  data() {
    return {
      loggIn: false,
      user: ""
    };
  },
  created() {
    this.loginCheck();
    this.getUser();
  },
  methods: {
    search() {
      this.$router.replace("buscar");
    },
    async getUser() {
      const user = await fb.auth().currentUser;
      if (user) {
        this.user = {
          displayName: user.displayName,
          email: user.email,
          phoneNumber: user.phoneNumber,
          photoURL: user.photoURL,
          providerId: user.providerId,
          uid: user.uid
        };
      }
    },
    logout() {
      fb.auth()
        .signOut()
        .then(() => {
          this.$router.replace("/");
        });
    },
    loginCheck() {
      fb.auth().onAuthStateChanged(user => {
        if (user) {
          this.loggIn = true;
        } else {
          this.loggIn = false;
        }
      });
    },
    reset() {
      Object.assign(this.$data, this.$options.data.apply(this));
    }
  }
};
</script>

<style scoped>
.navbar-toggler {
  border: 0;
}

.img-avatar {
  width: 1.8rem;
  height: 1.8rem;
  object-fit: cover;
  position: center center;
  border: solid 1px var(--white);
}
</style>
